	module.exports = {"data":{"markdownRemark":{"html":"<p>Congratulations on deciding to set up your hacker-powered security program on HackerOne! Here are the steps that'll get you up and running:</p>\n<ol>\n<li>\n<p>Go to hackerone.com.</p>\n</li>\n<li>\n<p>Click Sign Up.</p>\n</li>\n<li>\n<p>Fill out all fields on the form.</p>\n</li>\n<li>\n<p>Click <strong>Create Program</strong>. </p>\n</li>\n<li>\n<p>A confirmation email will be sent asking you to confirm your email address. Go to your email and click <strong>Confirm email address</strong>. </p>\n</li>\n<li>\n<p>You'll be taken to a page where you can select the type of program you'd like to launch. You can choose from:</p>\n<p>Option | Detail\n------ | -------\n<a href=\"%22/programs/start-h1-response%22\">HackerOne Response</a> | A private vulnerability disclosure program for organizations to receive and resolve security vulnerability reports from external third parties.\n<a href=\"%22/programs/start-h1-bounty%22\">HackerOne Bounty</a> | A continuous security program in which trusted hackers are incentivized to find critical vulnerabilities. </p>\n</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-113c5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 47.3831123517097%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"getting-started-1\"\n        title=\"\"\n        src=\"/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-e7ffa.png\"\n        srcset=\"/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-49813.png 125w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-dc8a5.png 250w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-e7ffa.png 500w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-dda24.png 750w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-c5f18.png 1000w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-22484.png 1500w,\n/docs.hackerone.com/static/getting-started-1-d8ee59cdc1a4fc115d6b69ecb9ded37f-113c5.png 2866w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Next Steps</h3>\n<ul>\n<li><a href=\"%22/programs/start-h1-response%22\">Start H1 Response</a></li>\n<li><a href=\"%22/programs/start-h1-bounty%22\">Start H1 Bounty</a></li>\n</ul>","frontmatter":{"path":"/programs/program-start-up-guide","title":"Program Start-Up Guide"}}},"pathContext":{}}

